import React from 'react';
import styles from './App.module.scss';
import { Link } from 'react-router-dom';
import { SaveImage } from '../../Assets/SaveImage';
import { getClasses } from '../../Helpers/cssHelper';
import { useDevMode } from '../../Services/DevMode/DevMode';
import { useBoussoleDataState } from '../../Services/BoussoleDataState/BoussoleDataState';

type PageLayoutProps = {
  /** optional header controls to be rendered */
  headerControls?: React.ReactElement;
};

/** renders a header with various buttons and the children as the content */
export const PageLayout: React.FunctionComponent<PageLayoutProps> = ({ headerControls, children }) => {
  const { isDev, setIsDev } = useDevMode();
  const { ongoingDiskOperation } = useBoussoleDataState();

  return (
    <>
      <header>
        {isDev && (
          <>
            <Link to="/editor2">Nouvel diteur</Link>
            <Link to="/editor-analysis">Analyse diteur</Link>
            <Link to="/editor-test">Test diteur</Link>
            <Link to="/icon-designer">diteur d'icnes</Link>
          </>
        )}
        {!isDev && <button onClick={() => setIsDev(true)}>&lt;/&gt;</button>}
        <Link to="/home">Home</Link>
        <Link to="/tasks">Tches</Link>
        <span title={ongoingDiskOperation ? "En cours d'enregistrement..." : 'Toutes les donnes sont sauvegardes.'}>
          <SaveImage
            className={getClasses(styles.saveButton, ongoingDiskOperation && styles.saving)}
            height={20}
            width={20}
          />
        </span>
        {headerControls}
      </header>
      <section>{children}</section>
    </>
  );
};
