import React, { FunctionComponent } from 'react';
import styles from './Field.module.scss';
import { getClasses } from '../../../Helpers/cssHelper';

export interface FieldProps {
  label: string;
  canMaximize?: boolean;
}

export const Field: FunctionComponent<FieldProps> = ({ label, canMaximize, children }) => {
  const [isMaximized, setIsMaximized] = React.useState(false);

  return (
    <div className={getClasses(styles.field, isMaximized && styles.isMaximized)}>
      <div className={styles.label}>
        {canMaximize && (
          <div className={styles.button} onClick={() => setIsMaximized(!isMaximized)}>
            
          </div>
        )}{' '}
        {label}
      </div>
      <div className={styles.children}>{children}</div>
    </div>
  );
};
