import React from 'react';
import { useBoussoleDataStore } from '../BoussoleDataStore/BoussoleDataStoreProvider';
import { BoussoleData } from '../../DataModel/BoussoleData';

export type IBoussoleDataState = {
  boussoleData: BoussoleData;
  ongoingDiskOperation: boolean;
  setBoussoleData: (setData: (boussoleData: BoussoleData) => BoussoleData) => void;
};

const BoussoleDataStateContext = React.createContext<IBoussoleDataState>(undefined as any);

export interface IBoussoleDataStateProviderProps {
  getNotReadyComponents: () => React.ReactElement | null;
  getReadyComponents: () => React.ReactElement | null;
}

/** Loads and manages the boussole data. Children are rendered if data is available, otherwise a "wait" message is being displayed. */
export const BoussoleDataStateProvider: React.FC<IBoussoleDataStateProviderProps> = ({
  getNotReadyComponents,
  getReadyComponents,
}) => {
  const dataStore = useBoussoleDataStore();
  const [state, setState] = React.useState<
    Pick<IBoussoleDataState, 'boussoleData' | 'ongoingDiskOperation'> | undefined
  >();

  const data = React.useRef<BoussoleData>();

  /** If 'savingAndPendingSave', the latest boussoleData has not yet been saved and needs saving. */
  const status = React.useRef<'saving' | 'savingAndPendingSave'>();

  // load initially the state. Called only once.
  React.useEffect(() => {
    (async () => {
      data.current = await dataStore.loadData();
      setState({ boussoleData: data.current, ongoingDiskOperation: false });
    })();
  }, [dataStore]);

  const setBoussoleData = React.useCallback(
    (setData: (boussoleData: BoussoleData) => BoussoleData) =>
      (async () => {
        data.current = setData(data.current!);
        setState({ boussoleData: data.current, ongoingDiskOperation: true });

        if (status.current) {
          status.current = 'savingAndPendingSave';
          return;
        }

        while (true) {
          status.current = 'saving';
          await dataStore.saveData(data.current);

          if (status.current === 'saving') {
            // no more update pending.
            status.current = undefined;
            setState({ boussoleData: data.current, ongoingDiskOperation: false });
            return;
          }
        }
      })(),
    [dataStore]
  );

  const boussoleDataState = React.useMemo<IBoussoleDataState | undefined>(
    () => state && { ...state, setBoussoleData },
    [state, setBoussoleData]
  );

  if (!boussoleDataState) {
    return <>{getNotReadyComponents()}</>;
  }

  return (
    <BoussoleDataStateContext.Provider value={boussoleDataState}>
      {getReadyComponents()}
    </BoussoleDataStateContext.Provider>
  );
};

export const useBoussoleDataState = () => {
  const boussoleDataState = React.useContext(BoussoleDataStateContext);
  if (!boussoleDataState) {
    throw new Error('Please define a BoussoleDataStateProvider upper in the component tree.');
  }
  return boussoleDataState;
};
