import React from 'react';
import { HashRouter as Router } from 'react-router-dom';
import { AppComponent } from './AppComponent';
import { AuthManagerProvider } from '../../Services/Auth/AuthManagerProvider';
import { BoussoleDataStoreProvider } from '../../Services/BoussoleDataStore/BoussoleDataStoreProvider';
import { BoussoleDataStateProvider } from '../../Services/BoussoleDataState/BoussoleDataState';
import { BoussoleDataServiceProvider } from '../../Services/BoussoleDataService/BoussoleDataServiceProvider';
import { LoadStatus } from '../LoadStatus/LoadStatus';
import { DevModeProvider } from '../../Services/DevMode/DevMode';

export const App: React.FC = () => {
  return (
    <AuthManagerProvider
      getNotAuthenticatedComponents={() => <LoadStatus done={0} total={2} message="Authentification en cours..." />}
      getAuthenticatedComponents={() => (
        <DevModeProvider>
          <BoussoleDataStoreProvider>
            <BoussoleDataStateProvider
              getNotReadyComponents={() => <LoadStatus done={1} total={2} message="Chargement des donnes..." />}
              getReadyComponents={() => (
                <BoussoleDataServiceProvider>
                  <Router>
                    <AppComponent />
                  </Router>
                </BoussoleDataServiceProvider>
              )}
            />
          </BoussoleDataStoreProvider>
        </DevModeProvider>
      )}
    />
  );
};
