import React from 'react';

export type DevModeContext = {
  isDev: boolean;
  setIsDev: (isDev: boolean) => void;
};

const DevModeContext = React.createContext<DevModeContext>(undefined as any);

export const DevModeProvider: React.FC = ({ children }) => {
  const [isDev, setIsDev] = React.useState(false);

  const context = React.useMemo<DevModeContext>(() => ({ isDev, setIsDev }), [isDev]);
  return <DevModeContext.Provider value={context}>{children}</DevModeContext.Provider>;
};

export const useDevMode = (): DevModeContext => React.useContext(DevModeContext);
