import React from 'react';
import { TextEditor2Model } from './TextEditor2Model';

export type TextEditor2Props = {
  model: TextEditor2Model;
  setModel: (model: TextEditor2Model) => void;
};

export const TextEditor2: React.FC<TextEditor2Props> = ({ model, setModel }) => {
  const rootDivRef = React.useRef<HTMLDivElement>(null);

  React.useLayoutEffect(() => {
    const engine = new TextEditor2Engine(rootDivRef.current!, model, setModel);
    return engine.unmount;
  }, [model, setModel]);

  return (
    <div ref={rootDivRef} contentEditable suppressContentEditableWarning={true}>
      <p style={{ background: 'blue' }} data-key="2" className="a b c">
        toto
      </p>
      <p style={{ background: 'lime' }} data-key="4">
        asdf
      </p>
      <p style={{ background: 'magenta' }}>to delete</p>
      blah blah
      <p style={{ background: 'yellow' }} data-key="1">
        tutu
      </p>
      hi guy
    </div>
  );
};

class TextEditor2Engine {
  private rootElement: HTMLDivElement;
  private model: InnerModel;
  private setModel: (model: TextEditor2Model) => void;
  private nextKey: number = 1;

  public constructor(
    rootElement: HTMLDivElement,
    model: TextEditor2Model,
    setModel: (model: TextEditor2Model) => void
  ) {
    this.rootElement = rootElement;
    this.model = this.getInnerModel(model);
    this.setModel = setModel;
    this.render();
  }

  private getInnerModel(model: TextEditor2Model): InnerModel {
    const result: InnerModel = { paragraphs: [] };
    for (let p of model) {
      result.paragraphs.push({
        key: this.nextKey++,
        text: p,
      });
    }
    return result;
  }

  private render() {
    var virtualDom = getVirtualDom(this.model);
    this.rootElement.normalize();
    this.renderNodes(this.rootElement, virtualDom);
  }

  private renderNodes(parent: HTMLElement, vNodes: VirtualDomNode[]) {
    const childNodesWithoutKey: ChildNode[] = [];
    const childNodesWithKey: { [key: number]: ChildNode } = {};

    // retrieve all child nodes of type TEXT_NODE or ELEMENT_NODE and store them in childNodesWithoutKey or childNodesWithKey.
    // other nodes are deleted.
    if (parent.hasChildNodes()) {
      const children = parent.childNodes;
      for (let i = 0; i < children.length; i++) {
        let childNode = parent.childNodes[i];

        if (childNode.nodeType !== Node.TEXT_NODE && childNode.nodeType !== Node.ELEMENT_NODE) {
          childNode.remove();
          i--;
          continue;
        }

        if (childNode.nodeType === Node.ELEMENT_NODE) {
          const el = childNode as HTMLElement;
          var keyStr = el.dataset.key;
          if (keyStr) {
            var key = parseInt(keyStr);
            if (!isNaN(key)) {
              if (childNodesWithKey[key] !== undefined) {
                // remove duplicate node with key
                childNode.remove();
                i -= 1;
              } else {
                childNodesWithKey[key] = childNode;
              }
              continue;
            }
          }
        }
        childNodesWithoutKey.push(childNode);
      }
    }

    let nextChildIndex = 0;

    let previousNode: ChildNode | null = null;
    for (const vNode of vNodes) {
      var nodeToAppend: ChildNode | undefined;
      if (vNode.type === 'text') {
        // try to find a text node with the correct text, removing any other text node
        while (nextChildIndex < childNodesWithoutKey.length) {
          const childNode = childNodesWithoutKey[nextChildIndex];
          if (childNode.nodeType !== Node.TEXT_NODE) {
            break;
          }
          const el = childNodesWithoutKey[nextChildIndex++] as Text;
          if (el.data === vNode.text) {
            nodeToAppend = el;
            break;
          }
          childNode.remove();
        }

        // no appropriate node was found, create a new one.
        if (!nodeToAppend) {
          nodeToAppend = document.createTextNode(vNode.text);
        }
      } else {
        // element

        // try to find get the existing node by key and make sure it has the correct tag name.
        nodeToAppend = childNodesWithKey[vNode.key];
        if (nodeToAppend) {
          delete childNodesWithKey[vNode.key];
          const el = nodeToAppend as HTMLElement;
          if (el.tagName !== 'P') {
            nodeToAppend.remove();
            nodeToAppend = undefined;
          }
        }

        // if not found, try to pick the next node without a key.
        if (!nodeToAppend)
          while (nextChildIndex < childNodesWithoutKey.length) {
            const childNode = childNodesWithoutKey[nextChildIndex];
            if (childNode.nodeType === Node.TEXT_NODE) {
              childNode.remove();
              continue;
            }
            nodeToAppend = childNodesWithoutKey[nextChildIndex++] as HTMLElement;
          }

        // no appropriate node was found, create a new one.
        if (!nodeToAppend) {
          nodeToAppend = document.createElement('P');
        }

        const el = nodeToAppend as HTMLElement;
        while (el.attributes.length > 0) {
          el.removeAttribute(el.attributes[0].name);
        }

        el.dataset.key = vNode.key.toString();

        this.renderNodes(el, vNode.children);
      }

      if (previousNode) {
        previousNode.after(nodeToAppend);
      } else {
        parent.prepend(nodeToAppend);
      }

      previousNode = nodeToAppend;
    }

    for (let node of childNodesWithoutKey) {
      node.remove();
    }

    for (var k in childNodesWithKey) {
      childNodesWithKey[k].remove();
    }
  }

  public unmount() {}
}

const getVirtualDom = (model: InnerModel): VirtualDomNode[] => {
  const result: VirtualDomNode[] = [];

  for (const p of model.paragraphs) {
    result.push({
      type: 'element',
      key: p.key,
      children: [
        {
          type: 'text',
          text: p.text,
        },
      ],
    });
  }

  return result;
};

type Paragraph = {
  key: number;
  text: string;
};

type InnerModel = {
  paragraphs: Paragraph[];
};

type VirtualDomNode = VirtualDomElement | VirtualDomTextNode;

type VirtualDomElement = {
  type: 'element';
  key: number;
  children: VirtualDomNode[];
};

type VirtualDomTextNode = {
  type: 'text';
  text: string;
};

